import server from '../../server.js';
export default {
	data() {
		return {
			postInfo:[],
			pllist:[],
			dslist:[],
			showVip:false,
			InputBottom: 0,
			plnr:"",
			xzId:1,
			postid:"",
			setvar:"",
			Give:"0",
			huifu:false,
			gaodu:'-710px',
			yanse:'rgba(0,0,0,0)',
			pluid:"",
			monnumber:1
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {// 来自页面内分享按钮
		  console.log(res.target)
		}
		return {
		  title: this.postInfo.Content,
		  path:'/pages/postinfo/postinfo?id='+this.postid,
		  desc:this.postInfo.Content,
		  imageUrl:this.postInfo.PictureId[0]
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		console.log(option.id); //打印出上个页面传递的参数。
		this.postid=option.id;
		this.getdsList(this.postid);
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=outPostInfo', 
			data: {
				id: option.id
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				console.log("————————————帖子详情——————————");
				this.postInfo=res.data.data;
				console.log(this.postInfo);
				uni.request({
					method:'GET',
					url: 'https://api.angeli.top/post.php?type=getpl', 
					data: {
						postid: option.id
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						console.log("————————————评论详情——————————");
						this.pllist=res.data.data;
						console.log(this.pllist);
						
					}
				});
			}
		});
		
		console.log(this.dslist)
	},
	
	methods: {
		getbieren:function(e){
			if(e==server.userinfo.Auid){
				uni.navigateTo({
					url: '../i/i'
				})
			}else{
				uni.navigateTo({
					url: '../i/bieren?auid='+e
				})
			}
		},
		getdsList:function(postid){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=getDashangList&postId="+postid, //仅为示例，并非真实接口地址。
				data: {
				
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.dslist=res.data.data
						console.log("打赏输出")
						console.log(this.dslist);
						this.$forceUpdate()
					}else{
						this.dslist=0;
					}
				},
				complete() {
					
				}
			});
		},
		getList:function(){
			uni.navigateTo({
				url:"dashang?id="+this.postid
			})
		},
		getDashang:function(){
			console.log(this.monnumber);
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=dashang", //仅为示例，并非真实接口地址。
				data: {
					toid: this.postInfo.AuthorId,
					postid:this.postid,
					number:this.monnumber
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.showVip=false;
						this.gaodu='-710px';
						this.yanse='rgba(0,0,0,0)'
						uni.showToast({
							title: "打赏成功！",
							position:'bottom',
							icon:'none'
						});
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
			
		},
		hideVip:function(){
			this.showVip=false;
			this.gaodu='-710px';
			this.yanse='rgba(0,0,0,0)'
		},
		xuanze:function(e){
			switch (e){
				case 1:
					this.monnumber=1
					break;
				case 2:
					this.monnumber=2
					break;
				case 3:
					this.monnumber=10
					break;
				case 4:
					this.monnumber=20
					break;
				default:
					this.monnumber=1
					break;
			}
			this.xzId=e
			/* 新思路,点赞改变列表状态可以使用该方法,:class="当前ID是否等于列子ID，如果是那么就点赞，如果不是就没有点赞，"
			但是,这样只能存在一个点赞状态,其他的状态就会消失,带思考 */
		},
		showKaitong:function(){
			this.showVip=true
			this.gaodu='0px'
			this.yanse='rgba(0,0,0,0.4)'
		},
		Like:function(postid,auid,give){
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postInfo.Give=true;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postInfo.Give=false;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		pinglun:function(e){
			this.plnr=e.target.value;
			if(this.plnr.indexOf('@')==0){
				this.huifu=true;
			}else{
				this.huifu=false;
			}
		},
		onShareAppMessage(res) {
			if (res.from === 'button') {// 来自页面内分享按钮
			  console.log(res.target)
			}
			return {
			  title: this.postInfo.Content,
			  path:'/pages/postinfo/postinfo?id='+this.postid,
			  desc:this.postInfo.Content,
			  imageUrl:this.postInfo.PictureId[0]
			}
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		sendpl:function(e){
			console.log(this.plnr);
			if(this.plnr=="null" ||this.plnr==""){
				uni.showToast({
					title: "请输入评论内容！",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.plnr.length>300 || this.plnr.length<1 ){
				uni.showToast({
					title: "内容最少1个字，最多300字",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			
			if(this.huifuid && this.huifu==true){
				//回复评论模式
				//console.log("回复评论",this.plnr)
				let qian=this.plnr.substring(0,this.plnr.indexOf(':')+1);
				let nr=this.plnr.replace(qian,"");
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=hfpl', //仅为示例，并非真实接口地址。
					data: {
						id: this.huifuid,
						txt:nr,
						postid:this.postid,
						uid:this.pluid,
						auid:server.userinfo.Auid
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.setvar="";
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									console.log(this.pllist);
									
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
					}
				});
				
			}else{
				//评论模式
				uni.request({
					method:'POST',
					url: 'https://api.angeli.top/post.php?type=pl', //仅为示例，并非真实接口地址。
					data: {
						auid: server.userinfo.Auid,
						txt:this.plnr,
						postid:this.postid,
						uid:this.postInfo.AuthorId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Cookie':server.cookie
					},
					success: (res) => {
						if(res.data.code=='1'){
							this.setvar="";
							uni.request({
								method:'GET',
								url: 'https://api.angeli.top/post.php?type=getpl', //仅为示例，并非真实接口地址。
								data: {
									postid: this.postid
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded',
									'Cookie':server.cookie
								},
								success: (res) => {
									console.log("————————————评论详情——————————");
									this.pllist=res.data.data;
									console.log(this.pllist);
									
								}
							});
						}
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
					}
				});
			}
		},
		dianzan:function(plid,index){
			console.log("点赞")
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=dianzan', //仅为示例，并非真实接口地址。
				data: {
					postid: plid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						var give=this.pllist[index].Give;
						console.log(give);
						give++;
						console.log(give);
						this.$set(this.pllist[index],`Give`,give);
					}
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none',
						duration:2000
					});
				}
			});
		},
		huifua:function(e,n,plid,uid){
			this.huifuid=e;
			this.setvar="@"+n+": ";
			this.huifu=true;
			this.pluid=uid;
			console.log("回复",e,n)

		}
	}
}
